@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* google fonts */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  margin: 0;
}
body {
  font-family: "Quicksand", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
}
.App {
  padding: 30px;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
}
.header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
h1 {
  font-size: 24px;
  margin-bottom: 15px;
}
a {
  text-decoration: none;
}
button {
  cursor: pointer;
  padding: 0;
  background: none;
  border: none;
  outline: none;
}
.btn {
  border-radius: 6px;
  width: 100px;
  height: 40px;
  background-color: skyblue;
}
.btn:hover {
  background-color: royalblue;
}

.showside-wrapper {
  border-radius: 6px;
  padding: 20px 50px;
  grid-column: 5 / span 8;
  grid-row: 1/8;
}
.recipes-wrapper {
  display: flex;
  grid-gap: 5%;
  gap: 5%;
  flex-wrap: wrap;
}
.recipe-card {
  width: 30%;
  margin-bottom: 20px;
}
.recipe-card p {
  text-align: center;
  font-size: 18px;
}

.pagination-part {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
}
.pagination-part p {
  font-size: 16px;
  padding-bottom: 2px;
}
.img-wrapper img {
  width: 100%;
}

@media only screen and (max-width: 600px) {
  .showside-wrapper {
    padding: 20px 10px;
  }
  .recipe-card {
    width: 90%;
    margin-left: 5%;
  }
}

.search-wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.searchside-wrapper {
  grid-column: 1 / span 4;
  grid-row: 1/2;
  border-radius: 6px;
  box-shadow: 0px 0px 12px #aaa, -0px -0px 12px #fff;
  margin: 0px 25px 0px 0px;
  padding: 25px;
}

.searchside-wrapper form {
  display: flex;
  flex-direction: column;
  grid-gap: 2%;
  gap: 2%;
  align-items: flex-end;
}

.searchside-wrapper label {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  grid-gap: 3%;
  gap: 3%;
  font-size: 17px;
  margin-bottom: 30px;
}

.searchside-wrapper label select {
  font-size: 16px;
  height: 25px;
  width: 70%;
}

.searchside-wrapper label input {
  font-size: 18px;
  padding-left: 5px;
  height: 30px;
  width: 275px;
}

@media only screen and (max-width: 600px) {
  .search-wrapper {
    display: block;
  }
  .searchside-wrapper {
    margin: 0;
    margin-bottom: 5%;
  }
  .searchside-wrapper label select {
    width: 60%;
  }
}

.recipe-detail-wrapper {
  padding: 70px 50px;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: auto;
}
.recipe-details {
  border-radius: 6px;
  padding: 50px;
  display: flex;
  justify-content: space-around;
}
.thumb .image-wrapper img {
  width: 500px;
}
.headings {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.headings p {
  font-weight: bold;
  font-size: 18px;
}
.ingredients {
  margin-left: 100px;
  width: 300px;
  font-size: 18px;
}
.ingredients li {
  margin-top: 4px;
}
.instructions {
  margin-left: 20px;
  width: 700px;
  font-size: 17px;
}

@media only screen and (max-width: 600px) {
  .recipe-detail-wrapper {
    padding: 30px 20px;
  }
  .recipe-details {
    flex-direction: column;
    padding: 10px;
  }
  .thumb .image-wrapper img {
    width: 100%;
  }
  .headings h2 {
    font-size: 15px;
  }
  .headings p {
    font-weight: bold;
    font-size: 12px;
  }
  .ingredients {
    margin: 0;
    padding: 5px 50px;
  }
  .instructions {
    margin: 0px;
    margin-top: 5px;
    padding: 10px;
    font-size: 17px;
    width: 100%;
    font-size: 17px;
  }
}

